// Importa as bibliotecas React e useState do React
import React, { useState } from 'react';

// Importa o BrowserRouter de 'react-router-dom' para gerenciar as rotas da aplicao
import { BrowserRouter as Router } from 'react-router-dom';

// Importa o componente Helmet da biblioteca 'react-helmet' para gerenciar o contedo da <head> do documento HTML
import { Helmet } from 'react-helmet';

// Importa o componente de rotas personalizado que gerencia a navegao na aplicao
import AppRoutes from './routes';

// Importa o objeto de tradues que contm strings em diferentes idiomas
import translations from '../AppComponents/translations';

// Importa o logotipo da aplicao que ser exibido na interface
import logo from '../assets/rsz_1design_inmotion_181818.png';

// Importa o arquivo CSS que contm os estilos globais da aplicao
import '../index.css';

// Funo principal do componente App, que  o componente raiz da aplicao
function App() {
  // Define um estado local 'showTools' que controla a visibilidade das ferramentas rpidas, inicialmente falso
  const [showTools, setShowTools] = useState(false);

  // Define um estado local 'language' que controla o idioma atual da aplicao, inicialmente 'en' (ingls)
  const [language, setLanguage] = useState('en');

  // Funo que alterna o idioma da aplicao entre ingls ('en') e portugus ('pt')
  const toggleLanguage = () => {
    setLanguage(lang => lang === 'en' ? 'pt' : 'en');
  };

  // Varivel 't' que acessa as strings de traduo de acordo com o idioma atual
  const t = translations[language];

  // Retorna a estrutura JSX que compe a interface da aplicao
  return (
    // Configura o Router para gerenciar as rotas da aplicao, com um basename que define a base para todas as URLs
    <Router basename="/NS3">
      {/* Div principal que define o layout da pgina com fundo preto, texto branco, e ajusta a tela para ocupar o mnimo da altura do ecr */}
      <div className="bg-black text-white min-h-screen flex flex-col items-center justify-center p-4">
        {/* Componente Helmet que define o ttulo da pgina e meta tags de descrio dinmicas com base no idioma atual */}
        <Helmet>
          <title>InMotion - Consulting</title>
          <meta name="description" content={t.description} />
        </Helmet>
        {/* Div que centraliza o contedo e define uma largura mxima para manter o layout responsivo */}
        <div className="w-full max-w-4xl text-center">
          {/* Componente AppRoutes que gerencia as rotas da aplicao */}
          {/* Passa para o componente AppRoutes os estados e funes necessrios, como idioma, alternncia de idioma, visibilidade das ferramentas rpidas, etc. */}
          <AppRoutes 
            language={language}          // Passa o estado do idioma atual
            toggleLanguage={toggleLanguage} // Passa a funo que alterna o idioma
            showTools={showTools}          // Passa o estado que controla a visibilidade das ferramentas rpidas
            setShowTools={setShowTools}    // Passa a funo que atualiza a visibilidade das ferramentas rpidas
            t={t}                          // Passa o objeto de tradues atual baseado no idioma selecionado
            logo={logo}                    // Passa o logotipo da aplicao para ser utilizado no layout
          />
        </div>
      </div>
    </Router>
  );
}

// Exporta o componente App como padro para ser utilizado em outras partes da aplicao
export default App;
